import tuiDatetime from "@/components/dateTime/dateTime"
var COS =require('../../utils/cos-wx-sdk-v5')
export default {
	components: {
		tuiDatetime,
	},
	data() {
		return {
			TabCur: 0,
			winHeight: "", //窗口高度
			currentTab: 0, //预设当前项的值
			type: 1,
			startYear: 1980,
			endYear: 2030,
			cancelColor: "#888",
			color: "#5677fc",
			setDateTime: "",
			year:new Date().getFullYear(),
			month:new Date().getMonth()+1
		}
	},
	onLoad() {
		let that = this;
		//  高度自适应
		uni.getSystemInfo({
			success: function(res) {
				let calc = res.windowHeight;
				that.winHeight = calc;
			}
		});
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 10
		},
		// 滚动切换标签样式
		switchTab: function(e) {
			let that = this;
			this.TabCur = e.detail.current;
		},
		show: function() {
			this.cancelColor = "#888";
			this.color = "#5677fc";
			this.setDateTime = "";
			this.startYear = 1980;
			this.endYear = 2030;
			this.type = 3;
			this.$refs.dateTime.show()
		},
		change: function(e) {
			console.log(e)
			this.year=e.year
			this.month=e.month
		},
	},
}
